import React, { useEffect, useState } from "react"
import { useNavigate } from "react-router-dom";

import axios from "axios";
import tokenOrganizer from './../util/tokenOrganizer';

import configApi from './../config/api';

export default function Profil() {

    const [profil, setProfil] = useState({});
    const [user, setUser] = useState({});

    useEffect(() => {
        let token = tokenOrganizer.getToken();

        axios
            .get(`${configApi.BASE_URL}/profile`, {
                headers: {
                    Authorization: "Bearer " + token
                }
            })
            .then((response) => {
                let data = response.data.data;
                setUser(data);
                let mahasiswa = data?.mahasiswa[0]
                setProfil(mahasiswa);
            }).catch((err) => {
                //   setOpen(true)
            });
    }, [])

    const navigate = useNavigate();

    function handleClickNavigate(path) {
        navigate(path);
    }

    return (
        <>
            <div className={`h-screen w-full grid content-center`}>
                <div className="relative h-[667px] bg-slate-100 w-[768px] mx-auto rounded-xl shadow-md overflow-hidden border-4 border-slate-50">
                    <section className="absolute top-0 left-0 w-full p-2" id="header">
                        <button className="bg-white hover:bg-slate-400 text-gray-600 border-1 border-neutral-700 hover:border-neutral-500 font-bold py-2 px-4 rounded inline-flex items-center"
                            type="button"
                            onClick={() => handleClickNavigate('/')}>
                            <span>Kembali</span>
                        </button>
                    </section>

                    <section id="content" className="h-full w-full flex flex-col">
                        <div className="h-[80px] w-full text-center">
                            <div className="h-full w-full flex justify-center items-center text-gray-800">
                                <h1 className="text-2xl">Profil Saya</h1>
                            </div>
                        </div>
                        <div className="h-[510px] w-full bg-white overflow-y-auto py-2 text-center flex flex-col">
                            <div className="flex alignment-center justify-center items-center flex-col">
                                <img
                                    alt='Avatar Mahasiswa'
                                    src={profil?.imageUrl ?? 'https://ui-avatars.com/api/?background=random&name=Avatar'}
                                    className="img-avatar-kasus rounded-full mt-4"
                                    id="avatar-profil"
                                />
                                <p className="mt-2">{profil?.nama ?? '-'}</p>
                                <p>NIM : {profil?.nim ?? '-'}</p>
                            </div>
                            <div className="grid grid-cols-2 divide-x-[3px] mt-5">
                                <div>Program Studi : {profil?.jurusan_nama ?? '-'}</div>
                                <div>Email  : {user?.email ?? '-'}</div>
                                <div>Semester : {profil?.semester ?? '-'}</div>
                                <div>Alamat : {profil?.alamat  ?? '-'}</div>
                                <div>Tahun Pelajaran : {profil?.tahun_pelajaran  ?? '-'}</div>
                                <div></div>
                            </div>
                        </div>
                    </section>

                    <section className="absolute bottom-0 left-0 h-[60px] w-full" id="footer">
                        <div className="absolute bottom-2 w-full text-center">
                            <p className={`text-md font-thin text-slate-500`}>Sinala Care v-0.0.1</p>
                            <p className={`text-sm font-thin text-slate-500`}>&copy; 2023 - Universitas Hang Tuah Surabaya</p>
                        </div>
                    </section>
                </div>
            </div>
        </>
    )
}